.contactList {
  display: flex;
}

.tel {
  color: var(--white);
  text-decoration: none;
}

.contactList {
  display: flex;
  align-items: center;
  justify-content: baseline;
  margin-left: 30px;
}

.item {
  display: flex;
}

.item:not(:first-child) {
  margin-left: 20px;
}

.langBlock {
  display: flex;
  align-items: center;
  justify-content: baseline;
  color: var(--white);
  margin-left: 30px;
}

.ukr {
  margin-right: 9px;
}

.ukr::after {
  content: "|";
  margin-left: 9px;
}

.userBlock {
  color: var(--white);
  display: flex;
  align-items: center;
  margin-left: 30px;
}

.link {
  margin-left: 10px;
  color: var(--white);
  text-decoration: none;
}
